.review {
  display: block;
  position: relative;
  padding: 24px 30px 52px;

  @media (min-width: $tablet-width) {
    display: grid;
    padding: 80px 50px 125px 45px;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: auto auto auto auto;
    background-image: url("../img/review@1x.jpg");
    background-position: 0 2%;
    background-repeat: no-repeat;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/review@2x.jpg");
      background-size: 350px 457px;
    }
  }

  @media (min-width: $desktop-width) {
    padding: 78px 50px 176px 222px;
    background: url("../img/review@1x.jpg") 6% 8% no-repeat;
  }
}

.review__title {
  margin: 4px 0 14px 0;
  font-family: $main-font;
  font-size: 25px;
  line-height: 30px;

  @include font;

  @media (min-width: $tablet-width) {
    margin: 0 0 7px 0;
    padding: 5px 5px;
    font-size: 43px;
    line-height: 50px;
  }

  @media (min-width: $desktop-width) {
    font-size: 53px;
    line-height: 57px;
    margin: 0 0 13px 0;
    padding: 7px 5px 0 2px;
  }
}

.review__text {
  width: 260px;
  margin: 14px 0 31px 0;
  font-family: $main-font;
  font-size: 15px;
  line-height: 30px;
  font-weight: normal;
  text-align: left;
  color: $dark-gray;

  @media (min-width: $tablet-width) {
    width: 668px;
    margin: 26px 0 30px 4px;
    grid-column: 1/3;
    grid-row: 2/3;
    font-size: 17px;
  }

  @media (min-width: $desktop-width) {
    width: 873px;
    margin: 24px 0 43px 3px;
  }
}

.review__author,
.review__contact {
  margin: 0;
  font-family: $main-font;
  font-size: 17px;
  line-height: 30px;

  @include font;

  @media (min-width: $tablet-width) {
    font-size: 18px;
    line-height: 24px;
  }
}

.review__author {
  @media (min-width: $tablet-width) {
    margin: 29px 0 0 4px;
  }

  @media (min-width: $desktop-width) {
    margin: 0 0 0 2px;
    letter-spacing: -0.5px;
  }
}

.review__contact {
  margin-bottom: 28px;
  font-weight: normal;
  color: $dark-gray;

  @media (min-width: $tablet-width) {
    margin: 7px 0 0 3px;
    letter-spacing: -0.5px;
    grid-column: 1/2;
    grid-row: 4/5;
  }
}

.review__button-feedback {
  margin: 0 auto;
  width: 260px;
  padding: 14px 66px;
  font-family: $main-font;
  font-size: 18px;
  line-height: 20px;

  @include button-text;
  @include button-view;

  &:hover {
    color: $white;
    background: linear-gradient(0deg, #444444, #444444);
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }

  &:focus {
    color: $white;
    background: linear-gradient(0deg, #444444, #444444);
  }

  @media (min-width: $tablet-width) {
    width: 175px;
    padding: 13px 26px 15px;
    margin: 0;
    grid-column: 2/3;
    grid-row: 1/2;
    align-self: start;
    justify-self: end;
    font-size: 17px;
    line-height: 20px;
  }

  @media (min-width: $desktop-width) {
    width: 175px;
    margin: 10px 0 0 0;
  }
}

.review__buttons {
  display: flex;
  margin: 0 auto 36px;
  width: 260px;

  @media (min-width: $tablet-width) {
    width: auto;
    margin: 39px 19px 0 0;
    grid-column: 2/3;
    grid-row: 3/5;
    justify-self: end;
  }

  @media (min-width: $desktop-width) {
    margin: 6px 19px 0 0;
  }
}

.review__button {
  padding: 13px 39px 10px;
  border: 1px solid $light-gray;
  background-color: $white;

  &:hover {
    color: $white;
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    border: none;
    background-color: transparent;
    padding: 0;
  }
}

.review__button--back {
  @media (min-width: $tablet-width) {
    margin-right: 37px;
  }
}

