.index-box {
  position: relative;
  z-index: 1;
  background: url("../img/hero-mobile@1x.jpg");
  background-position: 50% -15%;
  background-repeat: no-repeat;
  background-color: $gray-back;

  @media (min-resolution: 2dppx) {
    background-image: url("../img/hero-mobile@2x.jpg");
    background-size: 575px auto;
    background-position: 50% -17%;
  }

  @media (min-width: 768px) {
    border-top: 1px solid #cccccc;
    background: url("../img/hero-tablet@1x.jpg");
    background-position: 50% 0%;
    background-repeat: no-repeat;
    background-color: $gray-back;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-tablet@2x.jpg");
      background-size: 1150px auto;
    }
  }

  @media (min-width: 1150px) {
    background: url("../img/hero-desktop@1x.jpg");
    background-position: 50% 0%;
    background-repeat: no-repeat;
    background-color: $gray-back;

    @media (min-resolution: 2dppx) {
      background-image: url("../img/hero-desktop@2x.jpg");
      background-size: 1150px auto;
    }
  }
}

.index-box__title {
  padding: 71px 28px 120px;
  max-width: 300px;

  @media (min-width: $tablet-width) {
    padding: 154px 50px 372px;
    max-width: 660px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    padding: 156px 130px 355px;
    max-width: 900px;
  }
}

.index-box__list {
  @include list;
}

.index-box::after {
  @media (min-width: $tablet-width) {
    content: "";
    position: absolute;
    bottom: 0;
    left: 2px;
    width: 100%;
    height: 34px;
