  @media (min-width: $desktop-width) {
    bottom: 0;
    left: 2px;
    width: 100%;
    height: 34px;
    background-size: 1150px 34px;
    margin: 0 0 -11px -2px;
    background-repeat: repeat-x;
    background-image: url("../img/zigzag-fill.png");
  }
}
