.video {
  position: relative;
  border: 1px solid $light-gray;
  margin: 0 30px 58px;
  padding: 8px 0 32px 0;

  &:hover {
    box-shadow: 0 6px 10px rgba(0, 0, 0, 0.1);
  }

  &:active {
    box-shadow: 0 3px 5px rgba(0, 0, 0, 0.1);
  }

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    padding: 0;
    margin: 0 50px 64px;
  }

  @media (min-width: $desktop-width) {
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-template-rows: auto auto auto;
    margin: 0 50px 106px;
  }
}

.video__title {
  position: relative;
  width: 156px;
  padding: 0 0 11px 90px;
  font-family: $main-font;
  font-size: 17px;
  line-height: 24px;

  @include font;

  @media (min-width: $tablet-width) {
    order: 2;
    width: 340px;
    margin: 42px 0 0 16px;
    font-size: 20px;
  }

  @media (min-width: $desktop-width) {
    grid-row: 1/2;
    grid-column: 2/3;
    width: 178px;
    align-self: end;
    padding: 0;
    margin: 76px 0 26px 60px;
  }
}

.video__text {
  width: 232px;
  padding: 17px 0 12px 34px;
  font-family: $main-font;
  font-size: 13px;
  line-height: 24px;
  font-weight: normal;
  text-align: left;
  color: $dark-gray;

  @media (min-width: $tablet-width) {
    order: 3;
    width: 627px;
    margin: 0 0 0 6px;
    padding: 12px 0 12px 34px;
    font-size: 15px;
    line-height: 30px;
  }

  @media (min-width: $desktop-width) {
    grid-column: 2/3;
    grid-row: 2/3;
    width: 288px;
    padding: 0 0 0 60px;
    margin: 0;
  }
}

.video__film {
  margin: 0;

  @media (min-width: $tablet-width) {
    font-size: 0;
    order: 1;
    display: block;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    grid-column: 1/2;
    grid-row: 1/4;
  }
}

.video__film img {
  @media (min-width: $tablet-width) {
    width: 668px;
    height: 455px;
  }

  @media (min-width: $desktop-width) {
    width: 682px;
    height: 455px;
  }
}

