.video__button-buy {
  display: block;
  width: 192px;
  margin: 29px auto 0;
  font-family: $main-font;
  font-size: 17px;
  line-height: 16px;
  padding: 16px 7px 16px 8px;
  letter-spacing: 1px;


  @include button-text;
  @include button-view;

  &:hover {
    color: $white;
    background: linear-gradient(0deg, #444444, #444444);
  }

  &:active {
    color: rgba(255, 255, 255, 0.3);
  }

  &:focus {
    color: $white;
    background: linear-gradient(0deg, #444444, #444444);
  }

  @media (min-width: $tablet-width) {
    order: 4;
    width: 584px;
    padding: 16px 207px;
    margin-bottom: 45px;
    letter-spacing: 0;
  }

  @media (min-width: $desktop-width) {
    width: 230px;
    margin: 19px auto 51px;
    padding: 16px 30px;
    grid-column: 2/3;
    grid-row: 3/4;
    align-self: center;
  }
}

.video__title::before {
  content: "";
  position: absolute;
  bottom: 22px;
  left: 34px;
  width: 32px;
  height: 30px;
  background-repeat: no-repeat;
  background-image: url("../img/icon-video.svg");

  @media (min-width: $tablet-width) {
    bottom: 13px;
    left: 26px;
  }

  @media (min-width: $desktop-width) {
    bottom: 76px;
    left: 2px;
  }
}
